<template>
    <div class="container">
        <form class="row align-items-end" @submit.prevent="submitOrderForm()">
            <div class="col-12 form-group-header mb-3"> </div>
            <div class="col-12 col-sm-6 col-lg-4 form-group-1" :class="{'group-error': $v.order.address.recipientFullName.$error }">
                <label for="recipientFullName">. . . </label>
                <input v-model.trim="$v.order.address.recipientFullName.$model" type="text" id="recipientFullName">
            </div>
            <div class="col-12 col-sm-5 col-lg-3 pb-2  ">
                <form-error :target="$v.order.address.recipientFullName" param-name="minLength"> 5 </form-error>
                <form-error :target="$v.order.address.recipientFullName" param-name="maxLength"> 64 </form-error>
            </div>
            <div class="w-100"></div>
            <div class="col-12 col-sm-4 col-lg-4 form-group-1" :class="{'group-error': $v.order.address.contactMobilePhoneNumber.$error }">
                <label for="contactMobilePhoneNumber">  </label>
                <input v-model.trim="$v.order.address.contactMobilePhoneNumber.$model" type="text" id="contactMobilePhoneNumber">
            </div>
            <div class="col-12 col-sm-8 col-lg-4 pb-2  ">
                <form-error :target="$v.order.address.contactMobilePhoneNumber" param-name="minLength">    10 </form-error>
                <form-error :target="$v.order.address.contactMobilePhoneNumber" param-name="maxLength">   </form-error>
            </div>

            <hr class="w-100 m-2">
            <div class="col-12 col-sm-6 col-md-5 col-lg-4 form-group-1" :class="{'group-error': $v.order.address.city.$error }">
                <label for="city"></label>
                <input v-model.trim="$v.order.address.city.$model" type="text" id="city">
            </div>
            <div class="col-12 col-sm-5 col-md-4 col-lg-3 pb-2  ">
                <form-error :target="$v.order.address.city" param-name="minLength"> 2 </form-error>
                <form-error :target="$v.order.address.city" param-name="maxLength"> 64 </form-error>
            </div>

            <hr class="w-100 m-2">
            <div class="col-12 col-md-6 col-lg-5 form-group-1" :class="{'group-error': $v.order.address.region.$error }">
                <label for="region"></label>
                <input v-model.trim="$v.order.address.region.$model" type="text" id="region">
            </div>
            <div class="col-12 col-md-4 col-lg-3 pb-2  ">
                <form-error :target="$v.order.address.region" param-name="minLength"> 2 </form-error>
                <form-error :target="$v.order.address.region" param-name="maxLength"> 80 </form-error>
            </div>

            <div class="w-100"></div>
            <div class="col-12 col-sm-6 col-lg-4 form-group-1" :class="{'group-error': $v.order.address.street.$error }">
                <label for="street"></label>
                <input v-model.trim="$v.order.address.street.$model" type="text" id="street">
            </div>
            <div class="col-12 col-sm-6 col-lg-3 pb-2">
                <form-error :target="$v.order.address.street" param-name="minLength"> 2 </form-error>
                <form-error :target="$v.order.address.street" param-name="maxLength"> 64 </form-error>
            </div>

            <hr class="w-100 m-2">
            <div class="col-12 col-sm-6 col-md-3 col-lg-2 form-group-1" :class="{'group-error': $v.order.address.buildingNumber.$error }">
                <label for="buildingNumber"> </label>
                <input v-model.trim="$v.order.address.buildingNumber.$model" type="text" id="buildingNumber">
            </div>
            <div class="col-12 col-sm-6 col-md-3 pb-2  ">
                <form-error :target="$v.order.address.buildingNumber" param-name="minLength"> 1 </form-error>
                <form-error :target="$v.order.address.buildingNumber" param-name="maxLength"> 16 </form-error>
            </div>

            <div class="col-12 col-sm-6 col-md-3 col-lg-2 form-group-1" :class="{'group-error': $v.order.address.apartmentNumber.$error }">
                <label for="apartmentNumber"></label>
                <input v-model.trim="$v.order.address.apartmentNumber.$model" type="number" id="apartmentNumber">
            </div>
            <div class="col-12 col-sm-6 col-md-3 pb-2  ">
                <form-error :target="$v.order.address.apartmentNumber" param-name="minLength"> 1 </form-error>
                <form-error :target="$v.order.address.apartmentNumber" param-name="maxLength"> 16 </form-error>
            </div>

            <hr class="w-100 m-2">
            <div class="col-12 col-md-8 form-group-1">
                <label for="postOfficeNumber">      </label>
                <textarea rows="3" v-model.trim="$v.order.address.postOfficeNumber.$model" type="text" id="postOfficeNumber"></textarea>
            </div>
            <div class="col-12 col-md-4 pb-2 align-self-center">
                <form-error :target="$v.order.address.postOfficeNumber" param-name="maxLength"> 120 </form-error>
            </div>

            <div class="w-100"></div>
            <div class="col-12 form-group-header mt-3 mb-3">  </div>
            <div class="col-12 mt-3 mb-3">
                <div class="row mt-1 mb-1 d-none d-sm-flex">
                    <div class="col-sm-6 col-md-8 text-center font-weight-bold"></div>
                    <div class="col-sm-3 col-md-2 text-center font-weight-bold"></div>
                    <div class="col-sm-3 col-md-2 text-center font-weight-bold">  .</div>
                </div>
                <div class="row">
                    <div class="col order-items">
                        <ordered-item v-for="orderedItem in orderedItems" :key="orderedItem.id" :ordered-item="orderedItem"></ordered-item>
                    </div>
                </div>
            </div>
            <div class="col-12 col-md-8 form-group-1">
                <label for="comment">  </label>
                <textarea rows="8" v-model.trim="$v.order.comment.$model" id="comment"></textarea>
            </div>
            <div class="col-12 col-md-4 align-self-center pb-2">
                <form-error :target="$v.order.comment" param-name="maxLength"> 512 </form-error>
            </div>
            <div class="col-12">
                <div class="row mt-5 mb-5">
                    <button :disabled="$v.$invalid" class="col-auto submit-btn"> </button>
                </div>
            </div>
        </form>
    </div>
</template>

<script>
    import {mapActions, mapGetters} from "vuex";
    import CartItem from "../components/cart/CartItem";
    import { required, minLength, maxLength} from 'vuelidate/lib/validators';
    import errorLabel from "../components/ErrorLabel";
    import orderedItem from "../components/order/ProductOrdered";

    export default {
        data() {
            return {
                order: {
                    orderedItems: [],
                    address: {
                        recipientFullName: '',
                        contactMobilePhoneNumber: '',
                        city: '',
                        region: '',
                        street: '',
                        buildingNumber: '',
                        apartmentNumber: '',
                        postOfficeNumber: ''
                    },
                    comment: ''
                }
            }
        },
        validations: {
            order: {
                orderedItems: [],
                address: {
                    recipientFullName: {required, minLength: minLength(5), maxLength: maxLength(64)},
                    contactMobilePhoneNumber: {required, minLength:minLength(10), maxLength: maxLength(10)} ,
                    city: {required, minLength: minLength(2), maxLength: maxLength(64)},
                    region: {required, minLength: minLength(2), maxLength: maxLength(80)},
                    street: {required, minLength: minLength(2), maxLength: maxLength(64)},
                    buildingNumber: {required, minLength: minLength(1), maxLength: maxLength(16)},
                    apartmentNumber: {required, minLength: minLength(1), maxLength: maxLength(16)},
                    postOfficeNumber: {maxLength: maxLength(120)}
                },
                comment: {maxLength: maxLength(512)}
            }
        },
        computed: {
            ...mapGetters({
                orderedItems: 'getCartItemsList',
                totalCartPrice: 'totalCartPrice'
            })
        },
        components: {
            'cart-item' : CartItem,
            'form-error': errorLabel,
            'ordered-item': orderedItem
        },
        methods: {
            ...mapActions({
                getCart: 'getCartItems',
                createOrder: 'postOrder'
            }),
            submitOrderForm() {
                if (!this.$v.$invalid) {
                    this.order.orderedItems = this.generateOrderItems();
                    this.createOrder(this.order)
                        .then((data) => {
                            this.$swal.fire({
                                type: 'success',
                                title: '  !',
                                html: '  : ' + data.orderId
                            }).then(() => {
                                this.$router.push("/")
                            })
                        })
                        .catch(() => {
                            this.$swal.fire({
                                type: 'error',
                                title: '  !',
                                text: '    .'
                            })
                        })
                }
            },
            generateOrderItems() {
                return this.orderedItems.map(item => {
                    return {
                        product: {
                            id: item.product.id
                        },
                        quantity: item.quantity,
                        size: item.size
                    }
                })
            },
            validatePhoneNumber(number) {
                const re = /[^0-9]/gi;
                return number.replace(re, '');
            }
        },
        created() {
            this.getCart();
            if (!this.orderedItems.length > 0) {
                this.$router.push("/")
            }
        },
        watch: {
            'order.address.contactMobilePhoneNumber': {
                handler(newVal) {
                    this.order.address.contactMobilePhoneNumber = this.validatePhoneNumber(newVal);
                },
                deep: true
            }
        }
    }
</script>

<style scoped>
    @import "../assets/css/form.css";

    .order-items {
        overflow-x: auto;
    }

    .order-items div:nth-child(even) {
        background: #e6e6e6;
    }
</style>



